











































import Api from '../../../../../../api.js'
import App from '../../../../../../App.vue'
export default {
	data() {
		return {
			index: '',
			shop: {},
			background: '',
			isshow: true,
			inputvalue: '',
			circles: '',
			showed: false,
			showed1: false,
			showed2: true,
			Circle: [],
			circle_id: '',
			company_id: '0'
		}
	},
	onLoad(data) {
		var that = this;
		console.log(data)
		this.shop = JSON.parse(data.shop)
		console.log(this.shop)
		that.company_id = that.shop.company_id;
	},
	methods: {
		sure: function() {
			console.log(1)
			var that = this;
			uni.request({
				url: Api.bindingCircle(),
				method: "POST",
				header: {
					token: App.getToken(),
					"Content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					companyId: that.company_id,
					circleId: that.circle_id
				},
				success: function(res) {
					console.log(res)
					if (res.data.code == '200') {
						uni.navigateTo({
							url: '../../authorityManagement',
						});
					} else if(res.data.code=='1006'){
						that.showed1 = true;
						that.showed2 = false;
					} else{
						uni.showToast({
							title: '您没有权限',
							duration: 2000,
							icon:'none'
						})
					}
				}
			})
		},
		clearTimer() {
			if (this.timer) {
				clearTimeout(this.timer)
			}
		},
		getinput: function(event) {
			var that = this;
			that.clearTimer()
			// console.log(event.timeStamp)
			this.timer = setTimeout(() => {
				uni.request({
					url: Api.searchCircle(),
					header: {
						'Content-Type': 'application/json',
						token: App.getToken()
					},
					data: {
						circleName: that.inputvalue
					},
					success: function(res) {
						console.log(res)
						if (that.inputvalue != '') {
							that.Circle = res.data.circlelist;
							console.log(that.Circle);
						} else {
							that.Circle = [];
						}
					}
				});
			}, 500)
		},
		change: function(index) {
			var that = this;
			setTimeout(function() {
				that.circles = that.Circle[index].circlename
				that.showed = true
			}, 300)
			that.circle_id = that.Circle[index].circleid;
			console.log(that.circle_id);
			console.log(that.company_id);
		},
		cancel: function() {
			var that = this;
			that.showed = false;
		},
		recall: function() {
			var that = this;
			that.showed = false;
			that.showed1 = false;
			that.showed2 = true;
		}
	}
}
